<template>
  <div>
    <mt-header title="选择题">
      <router-link to="/exercise"
                   slot="left">
        <mt-button icon="back">返回</mt-button>
      </router-link>
    </mt-header>
    <div>
      <mt-radio :title="title"
                style="height:300px"
                v-model="radiovalue"
                :options="options"></mt-radio>
      <div style="height: 30px;margin: 10px;">
        <div v-show="answerOpen">正确答案是： {{optionsAll.choiceAnser[index]}}</div>
      </div>

      <div style="display: flex;
    justify-content: space-around;
    margin: 10px;">

        <mt-button size="small"
                   type="primary"
                   @click="last">上一题</mt-button>
        <mt-button size="small"
                   type="primary"
                   @click="answerOpen = true">查看答案</mt-button>
        <mt-button size="small"
                   type="primary"
                   @click="next">下一题</mt-button>

      </div>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  data () {
    return {
      radiovalue: '',
      title: '',
      options: [],
      optionsAll: {},
      index: 0,
      answer: [],
      answerOpen: false

    }
  },
  watch: {
    radiovalue (val) {
      this.answer[this.index] = val
    }
  },
  methods: {
    last () {
      this.answerOpen = false
      if (this.index > 0) {
        this.index--
        this.foramt()
        this.radiovalue = this.answer[this.index]
      }

    },
    next () {
      if (this.index < 23) {
        this.answerOpen = false
        this.index++
        this.foramt()
        this.radiovalue = this.answer[this.index]
      }

    },
    foramt () {
      let choice = this.optionsAll.choice[this.index]
      this.title = choice.title
      let arr = []
      Object.keys(choice.select).forEach(element => {
        arr.push({ label: element + '.' + choice.select[element], value: element })
      });
      this.options = arr

    }
  },
  created () {
    axios.get('/answer.json').then(res => {
      this.optionsAll = res.data
      let choice = res.data.choice[0]
      this.title = choice.title
      let arr = []
      Object.keys(choice.select).forEach(element => {
        arr.push({ label: element + '.' + choice.select[element], value: element })
      });
      this.options = arr


    })
  }
}
</script>